span {
    color: white;
}

.header {
    display: flex;
}

.counter, .settings {
    display: flex;
    flex-direction: column;
    background-color: #292d33;
    min-width: 350px;
    min-height: 250px;
    margin: 0 auto;
    padding: 50px;
    border: 5px solid #2f3bad;
}

.monitor {
    background-color: #2f3bad;
    font-size: 57px;
    border: 1px solid #2f3bad;
    margin-bottom: 30px;
    padding: 0 150px;
}

.buttons {
    border: 2px solid #2f3bad;
    padding: 50px;
    margin: 0 auto;
}

.button {
    border: none;
    margin-right: 20px;
    font-size: 50px;
    background-color: #2f3bad;
    cursor: pointer;
}

.error {
    color: red;
}

.settingsValue {
    display: flex;
    flex-direction: column;
    border: 2px solid #2f3bad;
    margin-bottom: 30px;
    padding: 10px;
}

.settingsInput {
    max-width: 50px;
}

.settingsButton {
    border: 2px solid #2f3bad;
    padding: 50px 156px;
    margin: 0 auto;
}

.set {
    border: none;
    font-size: 50px;
    background-color: #2f3bad;
    cursor: pointer;
}

.max {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.start {
    display: flex;
    justify-content: space-between;
}